.login{
    display: flex;
    align-items: center;
    justify-content: center;
    .form{
        display: flex;
        flex-direction: column;
        input{
            width: 300px;
            max-width: 300px;
            font-size: 18px;
            border: none;
            outline: none;
            padding: 10px 20px;
            margin: 10px 0;
            border-radius: 5px;
        }
        .btn{
            padding: 10px;
            font-weight: 600;
            font-size: 20px;
            background-color: #555;
            color: #ccc;
            border: none;
            border-radius: 5px;
        }
    }
}