.video{
    width: 100%;
    margin-left: 100px;
    .video-screen{
        width: 100%;
        display: flex;
        .video-content{
            width: 70%;
            .screen{
                width: 100%;
                border-radius: 10px;
                margin-top: 10px;
            }
            .video-title{
                width: 95%;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
                font-weight: bold;
                font-size: 26px;
                line-height: 45px;
            }
            .description-video{
                display: flex;
                justify-content: space-between;
                align-items: center;
                border-bottom: 1px solid #c2c2c2;
                padding: 15px 0;
                .like-share{
                    display: flex;
                    align-items: center;
                    .icon{
                        background-color: #333;
                        border: none;
                        border-radius: 20px;
                        padding: 8px 12px;
                        color: #c2c2c2;
                        display: flex;
                        align-items: center;
                        margin: 0 15px;
                        cursor: pointer;
                    }
                }
            }
            .chanel-description{
                display: flex;
                align-items: flex-start;
                justify-content: space-between;
                margin: 30px 0;
                .chanel-avatar{
                    border-radius: 50%;
                }
                .chanel-title{
                    margin-right: 20px;
                    .chanel-name{
                        font-weight: bold;
                        font-size: 26px;
                        line-height: 33px;
                    }
                    .chanel-publish{
                        font-weight: normal;
                        font-size: 13px;
                        line-height: 16px;
                        opacity: 0.4;
                    }
                    .chanel-subtitle{
                        max-width: 600px;
                        font-weight: normal;
                        font-size: 16px;
                        line-height: 22px;
                        opacity: 0.6;
                        margin: 10px 0 20px;
                    }
                    .btn-show{
                        background-color: transparent;
                        border: none;
                        font-weight: normal;
                        color: #c2c2c2;
                        font-size: 20px;
                        line-height: 25px;
                        opacity: 0.4;
                        cursor: pointer;
                    }
                }
                .chanel-subscribe{
                    background: #FF0000;
                    border-radius: 20px;
                    padding: 12px 15px;
                    font-weight: normal;
                    font-size: 16px;
                    line-height: 20px;
                    border: none;
                    text-align: center;
                    cursor: pointer;
                    color: #FFFFFF;
                }
            }
        }
        .recomemded-video{
            width: 30%;
            margin-top: 10px;
            .video-item{
                padding: 0 30px;
                margin-bottom: 30px;
                .video-screen{
                    width: 100%;
                    height: 200px;
                    border-radius: 10px;
                    margin: auto;
                }
                .video-title{
                    font-weight: bold;
                    font-size: 20px;
                    line-height: 20px;
                    margin: 10px 0;
                }
                .video-views{
                    font-weight: normal;
                    font-size: 16px;
                    line-height: 23px;
                    color: #C2C2C2;
                    mix-blend-mode: normal;
                    opacity: 0.4;
                }
            }
        }
    }
}

@media only screen and (max-width: 480px){
    .video{
        margin-left: 0;
        .video-screen{
            display: block;
            .video-content{
                width: 100%;
                .screen{
                    height: 200px;
                    margin-top: 0px;
                    border-radius: 0;
                    position: fixed;
                    top: 63px;
                    z-index: 1;
                }
                .video-title{
                    font-size: 16px;
                    margin-left: 15px;
                    padding-top: 200px;
                }
                .description-video{
                    width: 100%;
                    overflow-y: scroll;
                    padding-left: 15px;
                    &::-webkit-scrollbar{
                        height: 0;
                    }
                    .icon{
                        margin: 0 5px;
                    }
                }
                .chanel-description{
                    align-items: center;
                    padding: 0 15px;
                    .chanel-title{
                        margin-right: 5px;
                        .chanel-name{
                            font-size: 18px;
                        }
                        .chanel-subtitle,
                        .btn-show{
                            display: none;
                        }
                        
                    }
                    .chanel-subscribe{
                        padding: 0;
                        background-color: transparent;
                        color: red;
                    }
                }
                .chanel-avatar{
                    margin-right: 10px;
                }
            }
            .recomemded-video{
                width: 100%;
                .video-item{
                    padding: 0 15px;
                    &:last-child{
                        margin-bottom: 80px;
                    }
                }
            }
        }
    }
}