.library-users{
    width: 1000px;
    height: 87.7vh;
    display: flex;
    align-items: center;
    justify-content: center;
    .library-users-item{
        display: flex;
        align-items: center;
        margin-bottom: 25px;
        margin-left: 10px;
        .users-img{
            width: 40px;
            border-radius: 50%;
        }
        .users-name{
            margin-left: 15px;
            color: #898989;
        }
    }
    .login-icon{
        display: flex;
        align-items: center;
        margin: auto;
        font-size: 36px;
        border: 1px solid #aaa9a9;
        border-radius: 5px;
        padding: 5px;
        color: #aaa9a9;
    }
}
.library-users-item:last-child{
    margin-bottom: 60px;
}
@media only screen and (max-width:480px){
    .library-users{
        .login-icon{
            width: 100%;
            font-size: 24px;
            padding: 4px;
        }
    }
}