@font-face {
    font-family: 'Mulish';
    font-style: normal;
    font-weight: 400;
    src: local(''),
         url('./Fonts/mulish-v7-latin_cyrillic-regular.woff2') format('woff2'),
         url('./Fonts/mulish-v7-latin_cyrillic-regular.woff') format('woff'); 
}
@font-face {
    font-family: 'Mulish';
    font-style: normal;
    font-weight: 700;
    src: local(''),
         url('./Fonts/mulish-v7-latin_cyrillic-700.woff2') format('woff2'),
         url('./Fonts/mulish-v7-latin_cyrillic-700.woff') 
         format('woff');
}

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Mulish';
}
li{
    list-style-type: none;
}
a{
    text-decoration: none;
}
.body{
    width: 100%;
    min-height: 100vh;
    overflow: hidden;
}
.container{
    max-width: 1300px;
    margin: auto;
}

.row{
    display: flex;
    margin-top: 76px;
}
.dark{
    color: #fff;
    background-color: #17171E;
}
.light{
    color: #19202C;
    background-color: #fff;
}
::-webkit-scrollbar{
    width: 8px;
}
::-webkit-scrollbar-thumb{
    background-color: #cccc;
    border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover{
    background-color: #999393cc;
}

@media only screen and (max-width: 540px){
    .row{
        margin-left: 0;
    }
}