.header{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2;
    padding: 18px;
    .nav{
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        .header-left{
            display: flex;
            align-items: center;
            .menu-bar{
                border: none;
                background-color: transparent;
            }
            .logo{
                margin: 0 70px 0 28px;
            }
            .input-search-block{
                display: flex;
                align-items: center;
                position: relative;
                .input-search{
                    width: 500px;
                    max-width: 500px;
                    background: #EBEBEB;
                    padding: 8px 12px;
                    opacity: 0.3;
                    border-radius: 22px;
                    font-weight: normal;
                    font-size: 16px;
                    line-height: 20px;
                    border: none;
                    outline: none;
                }
            }
            .search-icon{
                position: absolute;
                right: 5px;
            }
        }
        .header-right{
            display: flex;
            align-items: center;
            .login-icon{
                display: flex;
                align-items: center;
                font-size: 16px;
                border: 1px solid #EBEBEB;
                border-radius: 5px;
                padding: 5px 10px;
                color: #c2c2c2;
            }
            .header-profile{
                background-color: transparent;
                margin-right: 40px;
                border: none;
                cursor: pointer;
                img{
                    border-radius: 50%;
                }
            }
        }
    }
}


@media only screen and (max-width: 540px) {
    .header{
        padding: 10px;
        .nav{
            .header-left{
                width: 100%;
                display: flex;
                .header-menu-bar{
                    display: none;
                }
                .logo{
                    margin-left: 0;
                }
                .input-search{
                    display: none;
                    input{
                        display: none;
                    }
                }
                .search-icon{
                    color: #fff;
                    margin-right: 24px;
                }
            }
            .header-right{
                .icon-header{
                    display: none;
                }
                .header-profile{
                    margin-right: 0;
                }
            }
        } 
    } 
}