.home{
    width: 100%;
    margin-left: 100px;
}
.home-user-link{
    max-width: 300px;
    display: flex;
    align-items: center;
    margin: 20px 0 20px 0;
    .home-user-img{
        width: 60px;
        border-radius: 50%;
        margin-right: 15px;
    }
    .home-user-title{
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 33px;
    }
}
.user-content{
    max-width: 100%;
    display: flex;
    overflow-x: scroll;
    &::-webkit-scrollbar{
        height: 0;
    }
    .user-content-item{
        margin: 15px;
    }
    .user-video{
        border-radius: 14px;
    }
    .subtitle{
        width: 230px;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 20px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
    }
    .description{
        display: flex;
        justify-content: space-between;
        font-weight: normal;
        font-size: 11px;
        line-height: 16px;
        color: #C2C2C2;
    }
}
@media only screen and (max-width: 540px) {
    .home{
        margin-left: 0;
        .home-user-link{
            width: 300px;
            margin-left: 15px;
        }
        .home-user-img{
            width: 40px;
        }
        .home-user-title{
            font-size: 16px;
        }
        .user-content{
            .user-content-item{
                margin: 0px 10px;
            }
        }
    }
}