.chanel{
    margin-left: 100px;
    .chanel-bg-img{
        width: 95%;
        height: 200px;
        border-radius: 10px;
    }
    .chanel-block{
        max-width: 1200px;
        display: flex;
        align-items: center;
        margin-top: 25px;
        .chanel-avatar{
            width: 80px;
            border-radius: 50%;
            margin-right: 15px;
        }
        .description-block{
            width: 100%;
            display: flex;
            justify-content: space-between;
            .chanel-name{
                font-weight: bold;
                font-size: 26px;
                line-height: 33px;
            }
            .chanel-subscribed{
                font-weight: normal;
                font-size: 13px;
                line-height: 16px;
                color: #C2C2C2;
            }
            .chanel-subscribe{
                background: #FF0000;
                font-weight: normal;
                font-size: 16px;
                line-height: 20px;
                border-radius: 30px;
                padding: 0 30px;
                color: #fff;
                margin-right: 25px;
                border: none;
            }
        }
    }
    .chanel-menu{
        display: flex;
        margin-top: 20px;
        a{
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 23px;
            margin-right: 50px;
        }
        .chanel-search{
            display: flex;
            align-items: center;
            cursor: pointer;
            input{
                background-color: transparent;
                border: none;
                outline: none;
                border-bottom: 1px solid #000;
            }
        }
    }
    .chanel-home{
        max-width: 1200px;
        display: flex;
        align-items: flex-start;
        margin: 50px 0;
        .home-content{
            width: 75%;
            display: flex;
            .trend-content{
                height: 250px;
                border-radius: 10px;
            }
            .home-content-description{
                max-width: 40%;
                padding: 15px;
                h2{
                    font-weight: bold;
                    font-size: 26px;
                    line-height: 34px;
                }
                p{
                    font-weight: normal;
                    font-size: 16px;
                    line-height: 22px;
                    margin-bottom: 20px;
                    opacity: 0.6;
                }
                span{
                    font-weight: normal;
                    font-size: 16px;
                    line-height: 22px;
                    color: #FFFFFF;
                    opacity: 0.4;
                }
            }
        }
        .recommended{
            width: 25%;
            .recomended-title{
                font-weight: normal;
                font-size: 16px;
                line-height: 20px;
                color: #C2C2C2;
            }
            .users-item{
                display: flex;
                align-items: center;
                margin: 20px 0;
                .users-avatar{
                    border-radius: 50%;
                }
                .name{
                    font-weight: bold;
                    font-size: 18px;
                    line-height: 23px;
                    margin-left: 15px;
                }
            }
        }
    }
    .chanel-videos{
        .chanel-name{
            margin-left: 15px;
        }
    }
    .video-list{
        width: 100%;
        display: flex;
        overflow-y: scroll;
        margin-top: 25px;
        .video-item{
            margin-right: 25px;
            img{
                width: 300px;
                height: 200px;
                border-radius: 10px;
            }
            .video-title{
                width: 300px;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
                margin: 8px 0;
            }
            .video-description{
                display: flex;
                justify-content: space-between;
                font-size: 14px;
                margin-bottom: 30px;
                color: #555;
            }
        }
        &::-webkit-scrollbar{
            height: 0;
        }
    }
}


@media only screen and (max-width: 540px){
    .chanel{
        margin-left: 0;
        .chanel-bg-img{
            width: 100vw;
            height: 150px;
        }
        .chanel-block{
            display: block;
            margin-top: 20px;
            margin-left: 10px;
            position: relative;
            .chanel-avatar{
                width: 60px;
                margin-right: 0;
                position: absolute;
                top: -55%;
            }
            .description-block{
                display: block;
                .chanel-subscribe{
                    padding: 5px 10px;
                    font-size: 14px;
                    margin-top: 15px;
                }
            }
        }
        .chanel-menu{
            width: 100vw;
            padding: 0 10px;
            overflow-y: scroll;
            margin-bottom: 30px;
            &::-webkit-scrollbar{
                height: 0;
            }
            a{
                margin-right: 15px;
            }
        }
        .chanel-home{
            display: none;
        }
        .recommended{
            display: none;
        }
        .video-list{
            width: 100%;
            display: block;
            margin-top: 20px;
            overflow-y: auto;
            .video-item{
                width: 90%;
                margin: auto;
                img{
                    width: 100%;
                    margin: auto;
                }
            }
        }
    }
}