.profile-list{ 
    width: 100vw;
    height: 100vh;
    background-color:rgba(255, 255, 255, 0);
    border: 1px solid #171717;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    .modal-content{
        width: 300px;
        height: 100vh;
        padding: 10px;
        border: .2px solid #898989;
        outline: none;
        position: absolute;
        top: -1px;
        right: 110px;
        background-color: #171717;
    }
    .profile-item{
        display: flex;
        align-items: center;
        color: #898989;
        font-size: 20px;
        span{
            margin-left: 10px;
        }
    }
}
.log-out{
    display: flex;
    align-items: center;
    background-color: transparent;
    border: none;
    font-size: 20px;
    color: #898989;
    cursor: pointer;
    margin-top: 15px;
}
@media only screen and (max-width: 480px){
    .profile-list{
        left: 0;
    }
    .profile-list{ 
        .modal-content{
            width: 280px;
            top: -1px;
            right: 80px;
        }
    }
}