.sidebar{
    width: 240px;
    height: 100vh;
    overflow-y: scroll;
    overflow-x: hidden;
    padding: 30px 25px;
    position: fixed;
    left: 0;
    z-index: 99;
    .sidebar-menu{
        margin-bottom: 50px;
        .menu-item{
            display: flex;
            align-items: center;
            margin-bottom: 20px;
            &:nth-child(3){
                margin-bottom: 50px;
            }
        }
        a{
            font-weight: normal;
            font-size: 16px;
            line-height: 20px;
            color: #898989;
            text-decoration: none;
            display: flex;
            align-items: center;
            span{
                margin-left: 15px;
            }
        }
        .active{
            color: red;
        }
    }
    .sidebar-title{
        font-style: normal;
        font-weight: bold;
        font-size: 18px;
        line-height: 23px;
    }
    .sidebar-users{
        margin: 40px 0 50px 0;
        .login-icon{
            width: 110px;
            display: flex;
            align-items: center;
            margin: auto;
            font-size: 16px;
            border: 1px solid #EBEBEB;
            border-radius: 5px;
            padding: 5px;
            color: #EBEBEB;
        }
        .users-item{
            display: flex;
            align-items: center;
            margin: 15px 0px;
            .users-img{
                width: 40px;
                height: 40px;
                border-radius: 50%;
                margin-right: 15px;
            }
            a{
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 20px;
                color: #898989;
            }
            .active{
                color: red;
            }
        }
    }
}
.bar-menu{
    width: 80px;
    height: 100vh;
    position: fixed;
    left: 0;
    .menu{
        .bar-item{
            display: flex;
            align-items: center;
            padding: 20px 0;
            flex-direction: column;
            color: #898989;
            span{
                font-size: 14px;
                margin-top: 5px;
            }
        }
        .active{
            color: red;
        } 
    }
}
.open-menu{
    width: 240px;
}
.close-menu{
    width: 70px;
}

@media only screen and (max-width: 540px){
    .sidebar{
        width: 100%;
        height: 10vh;
        right: 0;
        bottom: 0;
        border-top: 1px solid #c2c2c2;
        padding: 15px 10px;
        overflow: hidden;
        .sidebar-menu{
            display: flex;
            justify-content: space-around;
            margin: 0;
            .item-mobile{
                display: none;
            }
            .menu-item{
                margin-bottom: 0;
                &:nth-child(3){
                    margin-bottom: 0px;
                }
                a{
                    text-align: center;
                    margin-bottom: 0;
                    padding: 0;
                    flex-direction: column;
                }
                span{
                    font-size: 12px;
                    margin: 0;
                }
            }
        }
        .sidebar-title, .sidebar-users{
            margin-bottom: 0;
            display: none;
        }
    }
    .bar-menu{
        width: 100%;
        height: 10vh;
        bottom: -7px;
        right: 0;
        z-index: 999;
        border-top: 1px solid #c2c2c2;
        .menu{
            width: 100vw;
            display: flex;
            justify-content: space-around;
            .bar-item{
                padding: 10px 0;
            }
        }
    }
}